const MyFormComponent = () => {
  const [form] = Form.useForm();

  // const submitForm = ({ name, favoriteColor }) => {
  //   console.log(name, favoriteColor);
  //   form.resetFields();
  // };

  const onFinish = (values) => {
    console.log('Finish:', values);
    form.resetFields();
    axios.post(`${currentEnv.DOMAIN_URL}/domainList`, {
      partnerId: this.props.partnerId,
      domainList: values
    })
      .then(response => {
        axios.get(`${currentEnv.DOMAIN_URL}/domainList/${this.props.partnerId}`)
      }).then(response => {
        const data = response.data
        if (data.length > 0) {
          if (data[0].domainList.length > 0) {
            const tableData = data[0].domainList.map((element, index) => {
              return {
                key: (index + 1).toString(),
                domain: element.domain,
                enviroment: element.enviroment
              }
            })
            this.setState({
              data: tableData
            })
          }
        }
      }).catch(error => {
        console.log(error)
      });
  };

  return (
    <Form form={form} name="horizontal_login" layout="inline" onFinish={onFinish}>
      <Form.Item
        name="enviroment"
        rules={[{ required: true, message: 'Can not be empty!' }]}
      >
        <Input placeholder="Enviroment" />
      </Form.Item>
      <Form.Item
        name="domain"
        rules={[{ required: true, message: 'Can not be empty!' }]}
      >
        <Input placeholder="Domain" />
      </Form.Item>
      <Form.Item shouldUpdate={true}>
        {() => (
          <Button
            type="primary"
            htmlType="submit"
          >
            Add more
          </Button>
        )}
      </Form.Item>
    </Form>
  );
}



class DomainListTable extends React.Component {

  state = {
    data: [],
    // enviromentValue: "awdwad",
    // domainValue: "awdwd",
    editting: 0
  }


  componentDidMount = () => {
    // console.log(this.props.manageContent.partnerId)
    axios.get(`${currentEnv.DOMAIN_URL}/domainList/${this.props.manageContent.partnerId}`)
      .then(response => {
        // console.log(response.data);
        const data = response.data
        if (data.length > 0) {
          if (data[0].domainList.length > 0) {
            const tableData = data[0].domainList.map((element, index) => {
              return {
                key: (index + 1).toString(),
                domain: element.domain,
                enviroment: element.enviroment
              }
            })
            this.setState({
              data: tableData
            })
          }

        }

        // this.setState({
        //   isLogin: response.data.isLogin
        // })
      }).catch(error => {
        console.log(error)
      });
  }

  componentWillReceiveProps = (newProps) => {
    // console.log(newProps)
    if (newProps.manageContent.partnerId !== this.props.manageContent.partnerId) {
      console.log('abc');
      axios.get(`${currentEnv.DOMAIN_URL}/domainList/${newProps.manageContent.partnerId}`)
        .then(response => {
          // console.log(response.data);
          const data = response.data
          if (data.length > 0) {
            if (data[0].domainList.length > 0) {
              const tableData = data[0].domainList.map((element, index) => {
                return {
                  key: (index + 1).toString(),
                  domain: element.domain,
                  enviroment: element.enviroment
                }
              })
              this.setState({
                data: tableData
              })
            } else {
              this.setState({
                data: []
              })
            }

          } else {
            this.setState({
              data: []
            })
          }

          // this.setState({
          //   isLogin: response.data.isLogin
          // })
        }).catch(error => {
          console.log(error)
        });

    }
  }

  delete = (text) => {
    console.log(text)
  }

  edit = (record) => {
    console.log(record);
    this.setState({ editting: record.key })
  }

  onFinish = values => {
    console.log('Finish:', values);
    console.log(this.props)
    this.setState({
      enviromentValue: "",
      domainValue: ""
    })
  };



  submitEdit = (record) => {
    // console.log(record);
    this.setState({
      editting: "0"
    })
  }

  cancelEdit = (record) => {
    // console.log(record);
    this.setState({
      editting: "0"
    })
  }

  onChangeDomain = (e) => {
    console.log(e.target.value)
  }

  onChangeEnviroment = (e) => {
    console.log(e.target.value)
  }


  render() {
    return (
      <div className="domain_list">
        <MyFormComponent />
        <br />
        <br />

        <Table dataSource={this.state.data}>
          <Column
            title="Enviroment"
            dataIndex="enviroment"
            key="enviroment"
            width="300px"
            render={(text, record) => {
              // const domain = record.domain
              if (this.state.editting !== record.key) {
                // console.log(this.state.editting)
                // console.log(text)
                return text
              } else {
                return <Input defaultValue={text} onChange={this.onChangeEnviroment} size="middle" />
              }
            }}
          />
          <Column
            title="Domain"
            dataIndex="domain"
            key="domain"
            render={(text, record) => {
              // console.log(text)
              // const domain = record.domain
              if (this.state.editting !== record.key) {
                // console.log(this.state.editting !== record.key)
                // console.log(record.key)
                return <a href={text}>{text}</a>
              } else {
                return <Input defaultValue={text} onChange={this.onChangeDomain} size="middle" />
              }
            }}
          />
          <Column
            title="Action"
            key="action"
            render={(text, record) => {
              if (this.state.editting !== text.key) {
                return (
                  <Space size="middle">
                    <a onClick={() => this.edit(record)}>Edit</a>
                    <a onClick={() => this.delete(text)}>Delete</a>
                  </Space>
                )
              } else {
                return (
                  <Space size="middle">
                    <a onClick={() => this.submitEdit(record)}>Ok</a>
                    <a onClick={() => this.cancelEdit(record)}>Cancel</a>
                  </Space>
                )
              }
            }}
          />
        </Table>
      </div>
    );
  }
}


const mapStateToProps = state => ({
  manageContent: state.manageContent,
});

// const mapDispatchToProps = dispatch => ({
//   getManageContent: (partnerId) => {
//     dispatch(getManageContent(partnerId));
//   },
// });


export default connect(mapStateToProps, null)(DomainListTable);