import React from 'react';
import Login from './Page/Login/Login';
import HospitalContent from './Page/HospitalContent/HospitalContent';
import AppIcon from './Page/AppIcon/AppIcon';

const routes = [

	// {
	// 	path: '/',
	// 	exact: true,
	// 	main: props => <FormContent {...props} />
	// },
	{
		path: '/login',
		exact: false,
		main: props => <Login {...props} />
	},
	{
		path: '/hospital-content',
		exact: false,
		main: props => <HospitalContent {...props} />
	},
	{
		path: '/app-icon',
		exact: false,
		main: props => <AppIcon {...props} />
	}
];

export default routes;